<template>
	<div v-my-directive data-title="用户中心" class="usermain">
		<!-- 左边 -->
		<userleft></userleft>
		<div class="CatalogLayout">
			<!-- 引入文件的方式 -->
			<div class="Genericpadding bold">我的账户</div>

			<div v-if="form" class="Genericpadding articlebt">
				<div class="Horizontalspacing interval">
					<el-avatar :size="60" :src="form.Avatar">
						<div class="image-slot iconfont icon-setting-role"></div>
					</el-avatar>
					<div class="ThinLineLayout">
						<div class="bold">{{ user.username }}</div>
						<div class="center minfont grey">建议尺寸: 100×100px，支持: .jpg、.gif、.png格式的图片，图片大小请不要超过500kb</div>
					</div>
				</div>
				<el-upload class="upload-demo center" :http-request="Pictures" action="#" :show-file-list="false" :before-upload="handleBeforeUpload">
					<el-button type="primary">上传头像</el-button>
				</el-upload>
			</div>
			<div class="Genericpadding bold helf">
				<el-form v-if="form" :rules="rules" ref="form" :model="form" label-width="80px">
					<el-form-item label="用户名">
						<el-input style="width: 300px" v-model="form.username" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="用户组">
						<el-input style="width: 300px" v-model="form.name" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="邮箱账号">
						<el-input style="width: 300px" v-model="form.email" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="注册时间">
						<el-input style="width: 300px" v-model="form.Creationtime" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="用户昵称" prop="nickname">
						<el-input style="width: 300px" v-model="form.nickname" placeholder="请输入昵称"></el-input>
					</el-form-item>
					<el-form-item label="QQ号码" prop="QQ">
						<el-input style="width: 300px" v-model="form.QQ" placeholder="请输入联系qq"></el-input>
					</el-form-item>
					<el-form-item label="微信号码" prop="WeChat">
						<el-input style="width: 300px" v-model="form.WeChat" placeholder="请输入微信"></el-input>
					</el-form-item>
					<el-form-item label="telegram" prop="telegram">
						<el-input style="width: 300px" v-model="form.telegram" placeholder="请输入telegram"></el-input>
					</el-form-item>
					<el-form-item label="个性签名">
						<el-input style="width: 500px" resize="none" type="textarea" placeholder="请输入个性签名" v-model="form.signature" :autosize="{ minRows: 2, maxRows: 2 }" maxlength="30" show-word-limit> </el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="submitForm('form')">提交</el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</div>
</template>
<script>
import userleft from './../components/userleft'
import { dataRequest as api, filesRequest, automaticLogin } from '@/api/api'
import { mapState } from 'vuex'
export default {
	name: 'name',
	computed: {
		...mapState(['user']),
		mapboxMap1() {
			// return this.$store.state.mapbox.map;

			return this.user
		},
	},
	components: {
		// 组件
		userleft,
	},
	data() {
		return {
			//返回值
			form: '',
			rules: {
				nickname: [
					{ required: false, message: '请输入QQ号码', trigger: 'blur' },
					{ min: 3, max: 16, message: '长度在 3 到 16个字符', trigger: 'blur' },
				],
				QQ: [
					{ required: false, message: '请输入QQ号码', trigger: 'blur' },
					{ validator: this.validateQQ, trigger: 'blur' },
				],
				WeChat: [
					{ required: false, message: '请输入微信号码', trigger: 'blur' },
					{ validator: this.validateWeChat, trigger: 'blur' },
				],
				telegram: [
					{ required: false, message: '请输入Telegram号码', trigger: 'blur' },
					{ validator: this.validateTelegram, trigger: 'blur' },
				],
			},
		}
	},
	methods: {
		// 函数
		handleBeforeUpload(file) {
			const maxSize = 500 * 1024 // 500 KB in bytes
			if (file.size > maxSize) {
				this.$message.error('文件大小超过限制（最大 500 KB）')
				return false // 阻止上传
			}

			const allowedExtensions = ['jpg', 'jpeg', 'png', 'gif']
			const allowedMimeTypes = ['image/jpeg', 'image/png', 'image/gif']

			const fileExtension = file.name.split('.').pop().toLowerCase()
			const fileType = file.type.toLowerCase()

			if (!allowedExtensions.includes(fileExtension) || !allowedMimeTypes.includes(fileType)) {
				this.$message.error('不支持的文件类型')
				return false // 阻止上传
			}

			return true // 允许上传
		},
		async Pictures(obj) {
			console.log(obj)
			var formData = new FormData()
			formData.append('file', obj.file)

			let arr = await filesRequest(['post=pictureupload', formData])
			if (arr.code == 200) {
				// 重新获取用户信息
				automaticLogin()
				this.$message.success(arr.massage)
			} else {
				this.$message.error(arr.massage)
			}
		},

		async submitForm(formName) {
			this.$refs[formName].validate(async valid => {
				if (valid) {
					// 这里提交
					let arr = { nickname: this.form.nickname, QQ: this.form.QQ, WeChat: this.form.WeChat, telegram: this.form.telegram, signature: this.form.signature }
					arr = await api(['post=Datamodification', arr])
					if (arr.code == 909 || arr.code == 4004) {
						this.$message.error('数据为空')
					} else if (arr.code == 200) {
						this.$message.success('修改成功')
						automaticLogin()
					} else {
						this.$message.error('修改失败')
					}
					console.log(arr)
				} else {
					// 这里是验证不通过
					console.log('error submit!!')
					return false
				}
			})
		},
		// 验证
		validateQQ(rule, value, callback) {
			if (!value) {
				// 允许字段为空，直接通过验证
				callback()
			} else {
				const regExp = /^[1-9]\d{5,13}$/
				if (regExp.test(value)) {
					callback()
				} else {
					callback(new Error('必须为有效的QQ号码，长度在5到13个字符'))
				}
			}
		},
		validateWeChat(rule, value, callback) {
			if (!value) {
				// 允许字段为空，直接通过验证
				callback()
			} else {
				// 微信号码正则表达式，以字母开头，长度在6到20个字符之间
				const regExp = /^[a-zA-Z][a-zA-Z0-9_-]{5,19}$/
				if (regExp.test(value)) {
					callback()
				} else {
					callback(new Error('必须为有效的微信号码，以字母开头，长度在6到20个字符之间'))
				}
			}
		},
		validateTelegram(rule, value, callback) {
			if (!value) {
				// 允许字段为空，直接通过验证
				callback()
			} else {
				// Telegram号码正则表达式，以字母开头，长度在5到32个字符之间
				const regExp = /^[a-zA-Z][a-zA-Z0-9_-]{4,31}$/
				if (regExp.test(value)) {
					callback()
				} else {
					callback(new Error('必须为有效的Telegram号码，以字母开头，长度在5到32个字符之间'))
				}
			}
		},
	},
	mounted() {
		// 生命周期:
		this.form = this.user
	},
	watch: {
		mapboxMap1(newData, oldData) {
			console.log(newData, 'jiu', oldData)
			this.form = newData
		},
	},
}
</script>
<style scoped>
.image-slot {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 60px;
	height: 60px;
	background: var(--el-fill-color-light);
	color: var(--el-text-color-secondary);
	font-size: 30px;
}
.helf {
	height: 100%;
}
</style>
